<template>
  <v-card elevation="0" tile :color="bg_color">
    <v-container class="py-0">
      <v-row justify="center">
        <v-card elevation="0" tile class="px-0" width="1200" :color="bg_color">
          <v-fab-transition>
            <v-btn :color="theme_color" dark absolute top left fab x-large>
              <v-icon color="white">{{main_icon}}</v-icon>
            </v-btn>
          </v-fab-transition>
          <v-card-text>
            <v-list class="mb-10" :color="bg_color">
              <v-subheader class="px-0 mx-0 mt-5">
                <v-row justify="center" class="px-0 mx-0">
                  <v-col cols="3">
                    <span :class="theme_text_color + ' title'">
                      Coding Skills
                    </span>
                  </v-col>
                  <v-spacer></v-spacer>
                </v-row>
              </v-subheader>
              <v-row justify="center" class="px-0 mx-0">
                <v-col
                  cols="6"
                  v-for="(item, index) in skills"
                  :key="index"
                  align-self="center"
                  align="center"
                  class="py-0 my-0"
                >
                  <v-row>
                    <v-col cols="3">
                      <v-icon :color="item.color" x-large>
                        {{ item.icon }}
                      </v-icon>
                      <br />
                      <span class="subtitle-1 font-weight-light">
                        {{ item.name }}</span
                      >
                    </v-col>
                    <v-col align-self="center" align="center">
                      <v-progress-linear
                        :value="item.num"
                        :color="item.color"
                        height="20"
                      >
                        <template v-slot:default="{ value }">
                          <strong class="white--text"
                            >{{ Math.ceil(value) }}%</strong
                          >
                        </template>
                      </v-progress-linear>
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>
            </v-list>
          </v-card-text>
        </v-card>
      </v-row>
      <v-divider></v-divider>
    </v-container>
  </v-card>
</template>

<script>
export default {
  name: "education",
  data: () => ({
    main_icon: "mdi-coffee",
    bg_color: "grey lighten-5",
    theme_color: "brown darken-3",
    theme_text_color: "brown--text text--darken-3",
    proj_index: 0,
    skills: [
      {
        name: "Python",
        icon: "mdi-language-python",
        num: 80,
        color: "deep-purple darken-1",
      },
      {
        name: "JavaScript",
        icon: "mdi-language-javascript",
        num: 70,
        color: "amber darken-1",
      },

      {
        name: "MATLAB",
        icon: "$matlab_icon",
        num: 75,
        color: "blue-grey darken-1",
      },
      {
        name: "R",
        icon: "mdi-language-r",
        num: 55,
        color: "light-blue darken-3",
      },
      {
        name: "Vue.js",
        icon: "mdi-vuejs",
        num: 65,
        color: "teal darken-2",
      },
      {
        name: "D3.js",
        icon: "mdi-alpha-d-circle",
        num: 60,
        color: "orange darken-4",
      },
    ],
  }),
};
</script>
